body {
    font-size: 16px;
    line-height: 20px;
    font-family: Arial;
    padding: 10px;
    background-color: black;
    box-sizing:  border-box;
    font-family: 'Space Mono', monospace;
}

.container {
    display: grid;
}

input, button {
    font-size: 16px;
}

.container > div {
    padding: 30px;
    margin-bottom: 10px;
}

